.hero {
  position: relative;
  height: 100vh;
  overflow: hidden;
  background: #0a0a0a;
  color: white;
}

.hero-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
}

.hero-content {
  position: relative;
  z-index: 2;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 1rem;
}

/* Horizon outlined text */
.horizon-outlined {
  position: relative;
  z-index: 1;
  display: inline-block;
  font-weight: 800;
  color: transparent;
  -webkit-text-stroke: 2px rgba(255, 255, 255, 0.95);
  text-transform: none;
  letter-spacing: 0.5px;
}

.horizon-outlined::before {
  content: attr(data-text);
  position: absolute;
  left: 0;
  top: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  color: transparent;
  background: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  pointer-events: none;
}

.ripple-container {
  position: absolute;
  inset: 0;
  pointer-events: none;
}

.ripple-circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.15);
  animation: ripple 6s infinite ease-out;
}

@keyframes ripple {
  0% {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 0.5;
  }
  100% {
    transform: translate(-50%, -50%) scale(1.6);
    opacity: 0;
  }
}

/* vertical social icons on left side of hero */
.hero-socials-vertical{
  position: absolute;
  left: 1.25rem;
  top: 50%;
  transform: translateY(-50%);
  display:flex;
  flex-direction:column;
  gap:0.75rem;
  z-index:3;
}
.hero-socials-vertical a{color:#fff;opacity:0.9;display:inline-flex;align-items:center;justify-content:center;width:36px;height:36px;border-radius:4px}
.hero-socials-vertical a:hover{opacity:1;transform:translateX(2px)}
.hero-socials-vertical svg{width:20px;height:20px}

/* MC logo top-left */
.hero-mce-logo{
  position:absolute;
  top:16px;
  left:16px;
  width:100px;
  height:90px;
  object-fit:contain;
  z-index:6;
  filter: brightness(0) invert(1);
}