/* OurArtists.css */

.our-artists {
  width: 100%;
  min-height: 500px;
  height: auto;
  position: relative;
  overflow: hidden;
  background: #ffffff;
  padding: 2rem 0;
}

/* Wrapper */
.marquee-wrapper {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 0;
}

/* Section */
.image-marquee {
  width: 100%;
  height: 400px;
  background: #000000;
}

/* Container */
.marquee-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 0;
  box-shadow: none;
  transition: height 0.3s ease;
  background: #ffffff;
}

/* Fade edges */
.marquee-container::before,
.marquee-container::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 150px;
  z-index: 2;
  pointer-events: none;
}

.marquee-container::before {
  left: 0;
  background: linear-gradient(to right,
    rgba(255, 255, 255, 1) 0%,
    rgba(255, 255, 255, 0) 100%);
}

.marquee-container::after {
  right: 0;
  background: linear-gradient(to left,
    rgba(255, 255, 255, 1) 0%,
    rgba(255, 255, 255, 0) 100%);
}

@media (max-width: 768px) {
  .marquee-container::before {
    background: linear-gradient(to right,
      rgba(255, 255, 255, 1) 0%,
      rgba(255, 255, 255, 0) 100%);
  }

  .marquee-container::after {
    background: linear-gradient(to left,
      rgba(255, 255, 255, 1) 0%,
      rgba(255, 255, 255, 0) 100%);
  }
}

/* Track */
.marquee-track {
  position: absolute;
  display: flex;
  height: 100%;
  will-change: transform;
  backface-visibility: hidden;
  transform: translateZ(0);
}

/* Item */
.marquee-item {
  flex: none;
  overflow: hidden;
  position: relative;
  transition: transform 0.3s ease;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}

.marquee-item:hover {
  transform: scale(1.05) translateY(-8px);
}

.marquee-name {
  position: absolute;
  bottom: 8px;
  right: 8px;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  font-weight: bold;
  color: rgba(255, 255, 255, 0.8);
  writing-mode: vertical-rl;
  text-orientation: mixed;
  transform: rotate(180deg);
  white-space: nowrap;
  z-index: 2;
}

/* Image */
.marquee-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  backface-visibility: hidden;
  transform: translateZ(0);
  transition: all 0.3s ease;
}

/* Loader */
.marquee-loading {
  position: absolute;
  inset: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}

.marquee-loading::after {
  content: "";
  width: 40px;
  height: 40px;
  border: 3px solid #f3f3f3;
  border-top: 3px solid #3498db;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Dark mode */
@media (prefers-color-scheme: dark) {
  .image-marquee {
    background: #ffffff;
  }

  .marquee-container::before {
    background: linear-gradient(to right,
      rgba(255, 255, 255, 0) 0%,
      rgba(255, 255, 255, 0) 100%);
  }

  .marquee-container::after {
    background: linear-gradient(to left,
      rgba(255, 255, 255, 0) 0%,
      rgba(255, 255, 255, 0) 100%);
  }

  .marquee-loading {
    background: rgba(0,0,0,0.6);
  }
}

/* Chevron buttons */
.marquee-chevron {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255, 255, 255, 0.8);
  border: none;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 4;
  font-size: 24px;
  color: #000;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.marquee-chevron:hover {
  background: rgba(255, 255, 255, 1);
  transform: translateY(-50%) scale(1.1);
}

.marquee-chevron-left {
  left: 20px;
}

.marquee-chevron-right {
  right: 20px;
}