.marquee {
  position: relative;
  width: 100%;
  max-width: 100%;
  height: 100px;
  overflow-x: hidden;
  background-color: #ffffff;
  display: block !important;
  flex-direction: row !important;
  gap: 0 !important;
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #fffcfc;
}

.track {
  position: absolute;
  white-space: nowrap;
  height: 100%;
  display: flex;
  align-items: center;
}

.track > h1 {
  margin: 0;
  padding: 0 3rem;
  font-size: 3rem;
  font-family: Antonio;
  -webkit-text-fill-color: #ffffff;
  -webkit-text-stroke-width: 1.5px;
  -webkit-text-stroke-color: #000000;
  text-transform: uppercase;
  white-space: nowrap;
  line-height: 1;
}

@media (max-width: 768px) {
  .marquee {
    height: 80px;
    border-top: 1px solid #ffffff;
    border-bottom: 1px solid #fffcfc;
  }
  
  .track > h1 {
    font-size: 3rem;
    padding: 0 1.5rem;
  }
}