.privacy-policy {
  padding: 2rem;
  max-width: 900px;
  margin: 2rem auto;
  color: #ffffff;
  background: transparent;
  line-height: 1.6;
  font-family: Inter, Arial, sans-serif;
}
.privacy-policy h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
}
.privacy-policy h2 {
  font-size: 1.125rem;
  margin-top: 1.25rem;
}
.privacy-policy ul {
  margin-left: 1.25rem;
}
.privacy-policy a {
  color: #1a73e8;
}

/* top-left logo linking to home; white/inverted SVG at 90px */
.legal-top-logo-link {
  text-decoration: none;
  display: inline-block;
}
.legal-top-logo {
  position: absolute;
  top: 16px;
  left: 16px;
  width: 90px;
  height: auto;
  z-index: 9999;
  filter: invert(1) brightness(2);
}

/* top-right home button */
.legal-home-btn {
  position: absolute;
  top: 16px;
  right: 16px;
  z-index: 9999;
  padding: 10px 20px;
  border-radius: 60px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #ffffff;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.legal-home-btn:hover {
  background: rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.legal-home-btn:active {
  transform: scale(0.98);
}
