@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400&family=BBH+Bartle&display=swap');

.cookie-consent-root {
  position: fixed;
  inset: 0; /* ensure high z-index layer */
  pointer-events: none;
  z-index: 9999;
}

.cookie-consent-banner {
  pointer-events: auto;
  position: fixed;
  left: 20px;
  right: 20px;
  bottom: 24px;
  max-width: 1060px;
  margin: 0 auto;
  background: rgba(6, 10, 15, 0.95);
  color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 8px 30px rgba(2,6,23,0.6);
  display: flex;
  gap: 18px;
  padding: 18px;
  align-items: flex-start;
}

.cookie-consent-content {
  flex: 1 1 60%;
}

.cc-heading {
  margin: 0 0 6px 0;
  font-size: 20px;
  line-height: 1.15;
  font-family: 'BBH Bartle', Georgia, 'Times New Roman', serif;
  color: #fff;
}

.cc-text {
  margin: 0 0 14px 0;
  font-size: 14px;
  color: #e6eef8;
  font-family: 'Roboto', sans-serif;
}

.cc-actions {
  display: flex;
  gap: 10px;
  align-items: center;
}

.cc-btn {
  border: none;
  padding: 8px 14px;
  border-radius: 8px;
  cursor: pointer;
  font-family: 'Roboto', sans-serif;
  font-size: 10px;
}

.cc-accept { background: #00b894; color: #06202a; font-weight: 600; }
.cc-manage { background: transparent; color: #cfe8ff; border: 1px solid rgba(255,255,255,0.08); }
.cc-decline { background: transparent; color: #f6b3b3; border: 1px solid rgba(255,255,255,0.06); }

.cc-settings {
  width: 320px;
  border-left: 1px solid rgba(255,255,255,0.04);
  padding-left: 14px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.cc-section { display: flex; flex-direction: column; gap: 10px; }
.cc-row { display:flex; justify-content:space-between; align-items:center; gap: 12px; }
.cc-label { font-weight:600; color: #fff; font-size: 13px; }
.cc-desc { font-size: 12px; color: #cfe8ff; }
.cc-toggle { background: rgba(255,255,255,0.03); padding: 6px 10px; border-radius: 8px; cursor:pointer; color:#cfe8ff; }
.cc-toggle-disabled { background: rgba(255,255,255,0.02); padding: 6px 10px; border-radius:8px; color:#9bd; }

.cc-save { display:flex; justify-content:flex-end; }
.cc-save-btn { background:#2d74ff; color: #fff; font-weight:600; }

@media (max-width: 720px) {
  .cookie-consent-banner { flex-direction: column; left: 12px; right: 12px; bottom: 16px; padding: 14px; }
  .cc-settings { width: 100%; border-left: none; padding-left: 0; }
}
