.latest-videos-section {
  background: #000000;
  padding: 3rem 1rem;
  width: 100%;
  /* Prevent reflow/repaint issues during scroll and resize */
  contain: layout style paint;
}
.latest-videos-inner {
  max-width: 1200px;
  margin: 0 auto;
  color: #ffffff;
}
.latest-videos-title {
  margin: 0 0 1rem 0;
  font-size: 1.5rem;
  font-weight: 700;
  color: #ffffff;
}
.latest-videos-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.25rem;
  /* Stabilize grid during scroll/resize */
  will-change: auto;
}
.video-card {
  background: transparent;
  border-radius: 30px;
  overflow: hidden;
  display: block;
  /* Prevent layout shift during iframe state changes */
  contain: layout;
  height: 152px;
}
.video-wrap {
  width: 100%;
  height: 100%;
  position: relative;
  /* Prevent flickering during scroll intersection changes */
  backface-visibility: hidden;
}
.video-wrap iframe {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  border: 0;
  border-radius: 30px;
  /* Prevent iframe jank on mobile */
  will-change: auto;
}

@media (max-width: 900px) {
  .latest-videos-grid {
    grid-template-columns: 1fr;
  }
}
