.mcr-footer {
  background: #000000;
  color: #ccc;
  padding: 2rem;
  width: 100%;
  margin-top: 0;
  position: relative;
}

.mcr-footer-content {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}

.footer-section {
  flex: 1;
  min-width: 250px;
}

.footer-section h4 {
  color: #fff;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

/* Links */
.footer-links a {
  display: block;
  margin-bottom: 0.6rem;
  color: #ccc;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-links a:hover {
  color: #fff;
}

/* Subscribe */
.subscribe input[type="email"] {
  width: 100%;
  max-width: 320px;
  padding: 0.5rem;
  margin-bottom: 0.75rem;
  border: none;
  border-radius: 5px;
}

.subscribe label {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}

.subscribe input[type="checkbox"] {
  margin-right: 0.5rem;
}

.subscribe button {
  margin-top: 0.75rem;
  padding: 0.5rem 1.2rem;
  background: #1db954;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
}

.subscribe button:hover {
  opacity: 0.9;
}

.subscribe button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.newsletter-message {
  margin-top: 0.75rem;
  font-size: 0.85rem;
  padding: 0.5rem;
  border-radius: 4px;
  background: rgba(29, 185, 84, 0.2);
  color: #1db954;
  border: 1px solid #1db954;
}

.newsletter-message.error {
  background: rgba(220, 53, 69, 0.2);
  color: #dc3545;
  border-color: #dc3545;
}

.footnote {
  margin-top: 1rem;
  font-size: 0.75rem;
  color: #aaa;
  line-height: 1.4;
}

/* Copyright */
.copyright {
  margin-top: 2rem;
  font-size: 0.85rem;
  color: #666;
}

/* Back to Top */
.back-to-top-section {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 100px;
}

.back-to-top-btn {
  padding: 0.5rem 1rem;
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #ffffff;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  backdrop-filter: blur(10px);
  border-radius: 50px;
  font-size: 0.9rem;
  font-weight: 500;
}

.back-to-top-btn:hover {
  background: rgba(255, 252, 252, 0.2);
  transform: translateY(-3px);
}

.back-to-top-btn:active {
  transform: scale(0.95);
}

.contact-us-footer {
  display: inline-block;
  padding: 0.5rem 1rem;
  border: 2px solid rgba(255, 255, 255, 0.8);
  background-color: transparent;
  color: #ccc;
  text-decoration: none;
  border-radius: 4px;
  transition: all 0.3s ease;
  font-weight: 600;
}

.contact-us-footer:hover {
  color: #fff;
  border-color: #fff;
  background-color: rgba(255, 255, 255, 0.05);
}

@media (min-width: 768px) {
  .back-to-top-section {
    position: absolute;
    bottom: 7.5rem;
    right: 2rem;
    min-width: auto;
  }
}