/* Vertical menu on right side of hero */
.menu-vertical {
  position: absolute;
  right: 1.25rem;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  z-index: 3;
}

.menu-item {
  color: #fff;
  opacity: 0.9;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  text-decoration: none;
  transition: opacity 0.3s ease, transform 0.3s ease;
  width: 60px;
  text-align: center;
}

.menu-item:hover {
  opacity: 1;
  transform: translateX(-2px);
}

.menu-item span {
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  line-height: 1.2;
  font-family: Roboto, sans-serif;
  writing-mode: vertical-rl;
  text-orientation: mixed;
  border: 5px solid white;
  background-color: transparent;
  padding: 8px 6px;
  border-radius: 4px;
}

@media (max-width: 768px) {
  .menu-vertical {
    position: absolute;
    right: 1.25rem;
    top: 1.5rem;
    transform: none;
  }

  .menu-item span {
    font-size: 0.65rem;
    writing-mode: horizontal-tb;
    text-orientation: mixed;
    padding: 5px 4px;
  }
}
