:root {
  --color-primary: #1db954;
  accent-color: var(--color-primary);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  object-fit: cover;
}

/* ===== INFINITE GRID SECTION ===== */

.infinite-grid-section {
  position: relative;
  width: 100%;
  height: 80vh;
  background-color: #ffffff;
  overflow: hidden;
  font-family: sans-serif;
  cursor: grab;
}

.infinite-grid-section.grabbing {
  cursor: grabbing;
}

.viewport {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.grid-item {
  position: absolute;
  width: 110px;
  height: 110px;
  background-color: #ffffff;
  border-radius: 15px;
  overflow: hidden;
  will-change: transform, filter, opacity;
  cursor: inherit;
  transform-origin: center center;
}

.grid-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  pointer-events: none;
  user-select: none;
  -webkit-user-drag: none;
}
.grid-item img[src*="moneyculture.png"] {
  filter: invert(1);
}

.filter-btn {
  display: none;
}

.count-badge {
  display: none;
}

body {
  cursor: grab;
}

body.grabbing {
  cursor: grabbing;
}
